@import '../../styles/main.scss';
.container {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    width: 768px;
    max-width: 1440px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
    max-width: 100%;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
    max-width: 100%;
  }
}

